<template>
  <div>
	<button @click="toParents">To parents</button>
  </div>
</template>

<script>
export default {
	data() {
		return {
			message: "child"
		}
	},
	methods: {
		toParents: function() {
			this.$emit("toParents", this.message)
		}
	}
}
</script>

